.hero{
    background-color:#282828;
    color:white; 
}

.hero-row{
    min-height:500px;
    transition:all 500ms;
}

.intro{
    background-color:#F7F7F7;
    color:#282828;
    padding:20px;
    text-align:center;
    transition:all 500ms;
}

.hero-title{
    width:100%;
    text-align:center;
    margin:25px 0;
}

.large-header {
    position: relative;
    width: 100%;
    background: #282828;
    overflow: hidden;
    background-size: cover;
    background-position: center center;
    z-index: 1;
    transition:all 500ms;
 }
 
 .main-title {
    position: absolute;
    margin: 0;
    padding: 0;
    color: #F9F1E9;
    text-align: center;
    top: 50%;
    left: 50%;
    -webkit-transform: translate3d(-50%, -50%, 0);
    transform: translate3d(-50%, -50%, 0);
 }
 
 .demo .main-title {
    text-transform: uppercase;
    font-size: 4.2em;
    letter-spacing: 0.1em;
 }
 
 .main-title .thin {
    font-weight: 200;
 }
 
 @media only screen and (max-width: 768px) {
    .demo .main-title {
       font-size: 3em;
    }
 }

 .connect-with-me{
    transition:all 500ms;
 }

 .connect-with-me h3{
    margin-top:30px;
    font-size:22px;
 }

 .connect-with-me a{
   font-size:30px;
   color:white;
   padding:0 30px 0 10px;
 }

 .connect-with-me a:hover{
   color:#DFDFDF;
   text-decoration:none;
   transition:all 200ms;
 }

 .connect-icon-text{
    font-size:22px;
    margin-left:10px;
 }

