@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,
*{
    font-family: 'Source Code Pro', monospace;
}
nav{
    /* background-color:#282828; */
    position:fixed;
}

nav{
    color:white;
}

.nav-link{
    cursor:pointer;
    display: inline-block;
  position: relative;
  padding-bottom: 3px;
}

.nav-link:after {
    content: '';
    display: block;
    margin: auto;
    height: 3px;
    width: 0px;
    background: transparent;
    transition: width .3s ease, background-color .3s ease;
  }

  .nav-link:hover:after {
    width: 100%;
    background:white;
  }

.navbar-brand{
    cursor:pointer;
}

.menu-icons{
    margin:0 10px;
    color:white;
    font-size:22px;
    transition:all 200ms;
}

.menu-icons:hover{
    color:#DFDFDF;
    transition:all 200ms;
}

.navbar-brand{
    font-weight:bold;
}

.hero{
    background-color:#282828;
    color:white; 
}

.hero-row{
    min-height:500px;
    transition:all 500ms;
}

.intro{
    background-color:#F7F7F7;
    color:#282828;
    padding:20px;
    text-align:center;
    transition:all 500ms;
}

.hero-title{
    width:100%;
    text-align:center;
    margin:25px 0;
}

.large-header {
    position: relative;
    width: 100%;
    background: #282828;
    overflow: hidden;
    background-size: cover;
    background-position: center center;
    z-index: 1;
    transition:all 500ms;
 }
 
 .main-title {
    position: absolute;
    margin: 0;
    padding: 0;
    color: #F9F1E9;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
 }
 
 .demo .main-title {
    text-transform: uppercase;
    font-size: 4.2em;
    letter-spacing: 0.1em;
 }
 
 .main-title .thin {
    font-weight: 200;
 }
 
 @media only screen and (max-width: 768px) {
    .demo .main-title {
       font-size: 3em;
    }
 }

 .connect-with-me{
    transition:all 500ms;
 }

 .connect-with-me h3{
    margin-top:30px;
    font-size:22px;
 }

 .connect-with-me a{
   font-size:30px;
   color:white;
   padding:0 30px 0 10px;
 }

 .connect-with-me a:hover{
   color:#DFDFDF;
   text-decoration:none;
   transition:all 200ms;
 }

 .connect-icon-text{
    font-size:22px;
    margin-left:10px;
 }


.cta{
    background-color:white;
    color:black;
    border:1px solid black;
    font-weight:bold;
    padding:5px 15px;
    width:75%;
    box-shadow:3px 3px 0 black;
    transition:all 500ms;
}

.hover-class{
    background-color:black;
    color:white;
    border:1px solid white;
    letter-spacing:2px;
    text-decoration:none;
}

.buttonWrapper{
    margin:10px 0;
}
.project-wrapper{
    background-color:white;
    border:2px solid black;
    box-shadow:4px 4px 0 black;
    margin:20px 0;
}

.screenshot{
    width:100%;
    height:300px;
    background-size:cover;
    background-position: center;
    display:block;
    transition:all 500ms;
}

.project-description{
    padding:20px;
}

.screenshot-hover{
    -webkit-filter: grayscale(0.5) blur(5px);
            filter: grayscale(0.5) blur(5px);
}

.screenshot-overlay:before{
    content:  "View Live Application";
    position:absolute;
    width:80%;
    text-align:center;
    bottom:45%;
    left:10%;
    z-index:99;
    font-weight:bold;
    font-size:22px;
    color:#282828;
    text-shadow:2px 3px 4px white;
    cursor: pointer;
    transition:all 500ms;
}

.container-padding{
    padding-left:50px;
    padding-right:50px;
}
.sectionHeading{
    width:100%;
    text-align:center;
    padding:20px 0;
}

